body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Table Styles */
table {
  border-collapse: collapse;
  font-size: 12px;
  text-align: left;
  background-color: #ffffff;
}

thead tr {
  background-color: #003366;
  color: #ffffff;
}

th,
td {
  padding: 8px 12px;
}

tbody tr {
  border-bottom: 1px solid #dddddd;
}

tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

tbody tr:last-of-type {
  border-bottom: 2px solid #003366;
}

/* Button Styles */
button {
  padding: 5px 10px;
  font-size: 12px;
  color: #ffffff;
  background-color: #003366;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #00509e;
}

button:focus {
  outline: none;
}

/* Table Header */
th {
  background-color: #003366;
  color: #ffffff;
  font-weight: bold;
}

/* Link Styles */
a {
  color: #00509e;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Responsive Styles */
@media (max-width: 768px) {
  table {
    font-size: 12px;
  }

  button {
    padding: 4px 8px;
    font-size: 10px;
  }
}
