body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Form Container */
.form-container {
  border: 2px solid #003366;
  border-radius: 5px;
  padding: 20px;
  margin: 20px 0;
  background-color: rgba(255, 255, 255, 0.9);
}

/* Form Controls */
.form-group {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
}

.form-group label {
  width: 150px;
  font-weight: bold;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group input[type="date"] {
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  border: 1px solid #cccccc;
  border-radius: 3px;
  font-size: 14px;
}

.form-group input[type="text"]:disabled,
.form-group input[type="number"]:disabled,
.form-group input[type="date"]:disabled {
  background-color: #eeeeee;
}

/* Button Styles */
.button-container {
  display: flex;
  gap: 15px;
  margin-top: 10px;
}

button {
  padding: 10px 20px;
  font-size: 14px;
  color: #ffffff;
  background-color: #003366;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #00509e;
}

button:focus {
  outline: none;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .form-container {
    padding: 10px;
  }

  .form-group {
    flex-direction: column;
    align-items: flex-start;
  }

  .form-group label {
    width: 100%;
  }

  .form-group input[type="text"],
  .form-group input[type="number"],
  .form-group input[type="date"] {
    width: 100%;
  }

  button {
    padding: 8px 16px;
    font-size: 12px;
  }

  .button-container {
    flex-direction: column;
    gap: 10px;
  }
}
