body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Fieldset Styles */
fieldset {
  border: 2px solid #003366;
  border-radius: 5px;
  padding: 20px;
  margin: 20px 0;
  background-color: rgba(255, 255, 255, 0.9);
  display: grid;
  gap: 20px;
}

legend {
  font-size: 18px;
  font-weight: bold;
  color: #003366;
}

/* Form Controls */
input[type="radio"] {
  margin-right: 5px;
}

label {
  margin-right: 15px;
  font-weight: bold;
}

/* Aligning Form Elements Beside Each Other */
.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

select,
input[type="text"] {
  width: 150px;
  padding: 5px;
  margin: 5px 0;
  border: 1px solid #cccccc;
  border-radius: 3px;
  font-size: 14px;
}

select:disabled,
input[type="text"]:disabled {
  background-color: #eeeeee;
}

/* Button Styles */
.button-container {
  display: flex;
  gap: 15px;
  margin-top: 10px;
}

button {
  padding: 10px 20px;
  font-size: 14px;
  color: #ffffff;
  background-color: #003366;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #00509e;
}

button:focus {
  outline: none;
}

/* Restricting Input to Numbers Only */
input[type="text"]#weight {
  -moz-appearance: textfield;
}

input[type="text"]#weight::-webkit-outer-spin-button,
input[type="text"]#weight::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Responsive Styles */
@media (max-width: 768px) {
  fieldset {
    padding: 10px;
  }

  legend {
    font-size: 16px;
  }

  .form-group {
    flex-direction: column;
    align-items: flex-start;
  }

  button {
    padding: 8px 16px;
    font-size: 12px;
  }

  .button-container {
    flex-direction: column;
    gap: 10px;
  }
}
