/* Style for the navigation links */
nav ul {
  list-style-type: none;
  padding: 0;

  display: flex;
  gap: 10px;
}

nav ul li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 10px 20px;
  background-color: #ed1c24;
  border: 1px solid #c4121e;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

nav ul li a:hover {
  color: white;
  background-color: #c4121e;
  border-color: #a10e16;
}

/* Style for the sections */
section {
  background: url("../FilterForm/logo.png") no-repeat center;
  background-size: cover;
  padding: 20px;
  border: 2px solid #005bac;
  border-radius: 8px;
  margin-bottom: 20px;
}

/* Style for the headings */
h2 {
  color: #005bac;
}

/* Style for the form labels and inputs */
.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #005bac;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group input[type="date"] {
  background-color: #f9f9f9;
}

.button-container button {
  background-color: #005bac;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
}

.button-container button:hover {
  background-color: #004080;
}
